<template>
    <van-nav-bar
        title="历史数据"
        fixed="true"
        left-arrow
        @click-left="back"
    />
    <ChangeDate @change-date="handleDateChange" >
        <template #content >
             <cardInner :isShowEchart="!isShowArr.includes(query.type)" @toggel="(isShow) => isEchart = isShow" >
                <template #inner >
                    <div  v-if="!isEchart && historyList.length" >
                        <DataList v-if="testArr.includes(query.type)" :keyList="enumData[query.type]" :historyList="historyList" />
                        <ItemImage v-else-if="query.type === '4'" :historyList="historyList" :keyList="enumData[query.type]" />
                        <ItemCell v-else-if="!testArr.includes(query.type)" :keyList="enumData[query.type]" :historyList="historyList"  />
                    </div>
                    <!-- <div v-if="isEchart && historyList.length" > -->
                        <LineEchart v-if="isEchart && historyList.length" v-for="(item) in keyList" :list="historyList" :desc="item" />
                    <!-- </div> -->
                    <div v-if="!historyList.length" class="empty" >暂无数据</div>
                </template>
            </cardInner>
        </template>
    </ChangeDate>
</template>

<script setup >
    import { ref } from "vue";
    import ChangeDate from "@/components/changeDate.vue";
    import { getHomeDetialHttp } from "../common/serviec";
    import DataList from "./component/dataList.vue"
    import LineEchart from "@/components/lineEchart.vue";
    import cardInner from "@/components/cardInner.vue";
    import { useRoute, useRouter } from "vue-router";
    import enumData, { isShowArr, testArr } from "./enum.js";
    import ItemCell from "./component/itemCell.vue"
    import ItemImage from "../../../components/itemImage.vue"
    let {back} = useRouter()
    let {query} = useRoute()
    let handleDateChange = (date) => {
        getHumanBodyDataList(date)
    }
    let keyList = ref(enumData[query.type])
    let historyList = ref([]);
    let isEchart = ref(false);
    let getHumanBodyDataList = async (date) => {
        try {
            let bloodsugarJson = {
                1: "3.9～6.1（空腹）",
                2: "3.9～6.1 （餐前）",
                3: "3.9～7.8（餐后）",
                4: "3.9～11.0（随机）"
            }
            let res = await getHomeDetialHttp(query.type, 0, date.startDate,  date.endDate, 0, 0);
            console.log("【获取人体成分】:", res)
            if(res.code !== 200) throw new Error(res.msg)
            query.type == 6 && (keyList.value[0][0].standard = bloodsugarJson[res.data.object[0].type])
            historyList.value = res.data.object;
            console.log("length", historyList.value.length)
        } catch (error) {
            console.error("【获取人体成分失败】:", error)
        }
    };
</script>

<style scoped >
    .empty{
        height: 50px;
        line-height: 50px;
        text-align: center;
        font-smooth: 13px;
        color: #aaa;
        background: #fff;
    }
</style>