<template>
    <div class="tabs" >
        <div @click="currentActive = item.key" class="tabs-item" v-for="(item) in date" :key="item.key" >
            <div :style="{borderColor: currentActive === item.key ? '#06B48D' : 'transparent' }" >{{item.name}}</div>
        </div>
    </div>
    <div class="tab-content" >
        <slot name="content" ></slot>
    </div>
</template>

<script setup >
    import { ref, watchEffect, defineEmits } from "vue";
import { DateUtil } from "../views/new-home/common/util/dateUtil";
    let date = [
        {name: "周", key: 1},
        {name: "月", key: 2},
        {name: "年", key: 3},
    ];
    let emits = defineEmits(["changeDate"])
    let currentActive = ref(1);
    watchEffect(() => {
        let json = {
            1: {startDate: DateUtil.getStartDayOfWeek(), endDate: DateUtil.getEndDayOfWeek()},
            2: {startDate: DateUtil.getStartDayOfMonth(), endDate: DateUtil.getEndDayOfMonth()},
            3: {startDate: DateUtil.getStartDayOfYear(), endDate: DateUtil.getEndDayOfYear()}
        };
        emits("changeDate", json[currentActive.value])
    })
</script>

<style scoped lang="less" >
.tabs{
    height: 53px;
    background-color: #fff;
    display: flex;
    align-items: center;
    .tabs-item{
        flex: 1;
        display: flex;
        justify-content: center;
        div{
            width: 20px;
            height: 53px;
            line-height: 53px;
            font-size: 14px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #333333;
            text-align: center;
            border-bottom: 3px solid transparent;
        }
    }
}
</style>